import {Injectable} from '@angular/core';
import {UserData} from "../models/user-data";
import {ApiHttpService} from "./api-http.service";
import {BaseUrl} from "../api-config";
import "rxjs/Rx";
import {Observable} from "rxjs";
import {forEach} from "@angular/router/src/utils/collection";

@Injectable()
export class ProfileService {

    constructor(private apiHttpService: ApiHttpService) {
    }

    getProfileInfo(id: number): Observable<UserData> {
        return this.apiHttpService.get(BaseUrl + "/api/profile/" + id.toString()).map(x => x.json()).map(x => {
            if (x != null){
                x.avatarSrc = this.resolveAvatarPath(x.avatarSrc);
            }
            return x;
        });
    }

    resolveAvatarPath(path: string) {
        if (path == null) {
            return "http://test.dev/avatar/testaccountXD/larger/15f544106f8a4f7389f9e46ff312794c.jpeg";
        }
        return BaseUrl + "/" + path;
    }

    getProfileFriends(id: number) {
        return this.apiHttpService.get(BaseUrl + "/api/profile/" + id.toString() + "/friends").map(x => x.json()).map(x=> {
            for (let friend of x){
                friend.avatarSrc = this.resolveAvatarPath(friend.avatarSrc);
            }
            return x;
        })
    }
}



